@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,400i,700,700i&display=swap);
.App {
  text-align: center;
  padding: 20px 0;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Comments {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.Comment {
	background-color: rgb(248, 248, 248);
	margin-top: 40px;
	width: 764px;
	height: 405px;
	overflow-y: auto;
	line-height: 21px;
	font: normal normal 400 normal 14px "Noto Sans", sans-serif;
	text-align: left;
}
.Comment p {
	padding-bottom: 3.5px;
	padding-left: 0;
	padding-right: 0;
	padding-top: 0;
	margin: 0;
}
.Comment * {
	line-height: 21px;
	word-break: break-word;
}
.controls {
	display: flex;
	justify-content: space-between;
	margin: 25px 0;
}
.controls button {
	font-size: 24px;
	padding: 5px 10px;
}
.ql-editor {
	outline: none;
	white-space: pre-wrap;
}
